import { Component, OnInit } from '@angular/core';
import { HomeService } from '../home.service';
import { ToastrManager } from 'ng6-toastr-notifications';

@Component({
  selector: 'app-post-form',
  templateUrl: './post-form.component.html',
  styleUrls: ['./post-form.component.scss'],
})
export class PostFormComponent implements OnInit {
  public userInfo = [];
  public stateId = 0;
  public cityList = [];
  public cityListArray = [];
  public zipCodeList = [];
  public locationList = [];

  constructor(public homeService: HomeService, public toastr: ToastrManager) {}

  ngOnInit(): void {
    this.userInfo = this.homeService.getUserInfoFromLocalStorage();
    this.getCities();
    this.homeService.updateObject.subscribe((data) => {
      console.log(`data received via post-AD ${JSON.stringify(data)}`);
    });
  }

  // executes when user clicks on city options
  onChangeCity(cityID: any) {
    if (cityID === undefined || cityID === '' || cityID === null) {
      // do nothing
    } else {
      console.log(`selected city details ${JSON.stringify(cityID)}`);
      setTimeout(() => {
        this.getZipCode(cityID);
        this.getLocationList(cityID);
      });
    }
  } // end of onChangeCity function

  // execute the function when user clicks on loaction options
  onChangeLocation(location: any) {
    console.log(`selected location Id ${JSON.stringify(location)}`);
  } // end of onChangeLocation Function

  // executes when user clicks on zipCode option
  onChangeZipCode(zipCode: any) {
    console.log(`selected zipCode Id ${JSON.stringify(zipCode)}`);
  } // end of onChangeZipCode Function

  // getting zipCode through cityID
  public getZipCode = (cityID: any) => {
    this.homeService.getZipCode(cityID).subscribe(
      (zipCode) => {
        if (zipCode[0].status === 'true') {
          this.zipCodeList = zipCode[0].data;
          console.log(
            `zip-code lists according to selected city ${JSON.stringify(
              this.zipCodeList
            )}`
          );
        } else {
          this.toastr.errorToastr(zipCode[0].message);
        }
      },
      // tslint:disable-next-line:no-shadowed-variable
      (error) => {
        this.toastr.errorToastr('Some error occured' + error);
      }
    );
  } // end of getZipCode function

  // getting location list through cityId
  public getLocationList = (cityID: any) => {
    this.homeService.getLocationLists(cityID).subscribe(
      (locationList) => {
        if (locationList[0].status === 'true') {
          this.locationList = locationList[0].data;
          console.log(
            `location list according to selected city ${JSON.stringify(
              this.locationList
            )}`
          );
        } else {
          this.toastr.errorToastr(locationList[0].message);
        }
      },
      // tslint:disable-next-line:no-shadowed-variable
      (error) => {
        this.toastr.errorToastr('Some error occured' + error);
      }
    );
  } // end of get location list function

  // getting cities
  getCities: any = () => {
    return this.homeService.getCity(this.stateId).subscribe(
      (response) => {
        console.log(response);
        if (response[0].status === 'true') {
          this.cityList = response[0].data;
          for (const object of this.cityList) {
            this.cityListArray.push(object.cityName);
            console.log(object.cityName);
          }
          console.log(this.cityListArray);
          console.log(this.cityList);
        } else {
          this.toastr.errorToastr(response[0].message);
        }
      },
      // tslint:disable-next-line:no-shadowed-variable
      (error) => {
        this.toastr.errorToastr('Some error occured' + error);
      }
    );
  } // end of getCities

  public postSpaceAds = () => {};
}
