import { Component, OnInit } from '@angular/core';
import { HomeService } from '../home.service';
import { ToastrManager } from 'ng6-toastr-notifications';

@Component({
  selector: 'app-post-ads',
  templateUrl: './post-ads.component.html',
  styleUrls: ['./post-ads.component.scss'],
})
export class PostAdsComponent implements OnInit {
  public categoryLists = [];
  categoryID: any;
  spaceCategory = [];
  public usedEquipment = [];
  jobsCategory: any = [];
  public dealsCategory = [];
  public spacesTypeList = [];
  public usedEquipmentTypeList = [];
  public jobsTypeList = [];
  public dealsTypeList = [];
  public jobLevelList = [];
  public data = {
    categorytypeID: '',
    categorytypeName: '',
    categoryID: '',
    subCatID: '',
  };

  constructor(public homeService: HomeService, public toastr: ToastrManager) {}

  ngOnInit(): void {
    setTimeout(() => {
      this.makeItFalse();
    });
    setTimeout(() => {
      this.getCategoriesLists();
      this.spacesList();
      this.usedEquipmentList();
      this.jobsList();
      this.dealsList();
      this.getCategoryTypeListsOfSpaces();
      this.getCategoryTypeListsOfUsedEquipment();
      this.getCategoryTypeListsOfJobs();
      this.getCategoryTypeListsOfDeals();
    }, 1000);
  }

  // getting category data
  getCatData(rData: any) {
    this.data.categoryID = rData.categoryID;
    this.data.subCatID = rData.subcatID;
  }// end of getCatData

  // get type
  public getType(categorytype: any) {
    this.data.categorytypeID = categorytype.categorytypeID;
    this.data.categorytypeName = categorytype.categorytypeName;
    console.log(
      `categorytypeID from post-ads component ${JSON.stringify(this.data)}`
    );
    setTimeout(() => {
      this.homeService.updateSpaceObject(this.data);
    }, 2000);
  }// end of get type

  // getting jobs related data when clicking on any particular level
  public getJobsRelatedData(data: any){
    console.log(`jobs related data ${JSON.stringify(data)}`);
  }// end of get-job-related funcyion

  // getting getCategoryTypeListsOfSpaces
  public getCategoryTypeListsOfSpaces = () => {
    const spaceCategoryId = 11;
    this.homeService.getCategoryTypeList(spaceCategoryId).subscribe((type) => {
      this.spacesTypeList = type[0].data;
      console.log(`category type list ${JSON.stringify(this.spacesTypeList)}`);
    });
  }// end of getCategoryTypeListsOfSpaces

  // getting getCategoryTypeListsOfEquipment
  public getCategoryTypeListsOfUsedEquipment = () => {
    const usedEquipmentCategoryId = 12;
    this.homeService
      .getCategoryTypeList(usedEquipmentCategoryId)
      .subscribe((type) => {
        this.usedEquipmentTypeList = type[0].data;
        console.log(
          `category type list ${JSON.stringify(this.usedEquipmentTypeList)}`
        );
      });
  }// end getCategoryTypeListsOfEquipment

  // getting getCategoryTypeListsOfJobs
  public getCategoryTypeListsOfJobs = () => {
    const jobsCategoryId = 13;
    this.homeService.getCategoryTypeList(jobsCategoryId).subscribe((type) => {
      this.jobsTypeList = type[0].data;
      console.log(`category type list ${JSON.stringify(this.jobsTypeList)}`);
    });
  }// end of getCategoryTypeListsOfDeals

  // getting category-type list of deals
  public getCategoryTypeListsOfDeals = () => {
    const dealsCategoryId = 14;
    this.homeService.getCategoryTypeList(dealsCategoryId).subscribe((type) => {
      this.dealsTypeList = type[0].data;
      console.log(`category type list ${JSON.stringify(this.dealsTypeList)}`);
    });
  }// end of getCategoryTypeListsOfDeals

  // getting category-list
  public getCategoriesLists: any = () => {
    return this.homeService.getCategoryList().subscribe(
      (response) => {
        // this.getDeals();
        this.categoryLists = response[0].data;
        // sort by value
        this.categoryLists.sort((a, b) => {
          return a.categoryID - b.categoryID;
        });
        console.log(response);
        console.log(
          `this is called from post-ads component ${JSON.stringify(
            this.categoryLists
          )}`
        );
      },
      (error) => {
        this.toastr.errorToastr('Some error occured ' + error);
      }
    );
  }// end of category-list

  // getting spacesList
  public spacesList = () => {
    const id = 11;
    // getting space sub-category data
    this.homeService.getSubCategoryList(id).subscribe((response) => {
      if (response[0].status === 'true') {
        this.spaceCategory = response[0].data;
        // sort by value
        this.spaceCategory.sort((a, b) => {
          return a.subcatID - b.subcatID;
        });
        console.log(this.spaceCategory);
      }
    }); // end of sub category request call
  }// end of spacesList

  // getting usedEquipmentList
  public usedEquipmentList = () => {
    const id = 12;
    // getting space sub-category data
    this.homeService.getSubCategoryList(id).subscribe((response) => {
      if (response[0].status === 'true') {
        this.usedEquipment = response[0].data;
        // sort by value
        this.usedEquipment.sort((a, b) => {
          return a.subcatID - b.subcatID;
        });
        console.log(this.usedEquipment);
      }
    }); // end of sub category request call
  }// end of usedEquipmentList

  // getting jobsList
  public jobsList = () => {
    const id = 13;
    // getting space sub-category data
    this.homeService.getSubCategoryList(id).subscribe((response) => {
      if (response[0].status === 'true') {
        this.jobsCategory = response[0].data;
        // sort by value
        this.jobsCategory.sort((a, b) => {
          return a.subcatID - b.subcatID;
        });
        // getting jobs level list
        this.homeService.getSubCategoryLevelList(id).subscribe(
          levelList => {
            this.jobLevelList = levelList[0].data;
          }
        ); // end of jobs level list
        console.log(this.jobsCategory);
      }
    }); // end of sub category request call
  }// end of jobsList

  // getting dealsList
  public dealsList = () => {
    const id = 14;
    // getting space sub-category data
    this.homeService.getSubCategoryList(id).subscribe((response) => {
      if (response[0].status === 'true') {
        this.dealsCategory = response[0].data;
        // sort by value
        this.dealsCategory.sort((a, b) => {
          return a.subcatID - b.subcatID;
        });
        console.log(this.dealsCategory);
      }
    }); // end of sub category request call
  }// end of dealsList

  public makeItFalse() {
    const isTrue = false;
    this.homeService.nextCountBoolean(isTrue);
  }
}
